*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Helvetica Neue, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  line-height: 1.5;
}

button, label {
  cursor: pointer;
}

input {
  font: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.timer {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

.clockface {
  justify-content: space-between;
  gap: 45px;
  font-family: Roboto Mono, monospace;
  font-size: 40px;
  display: flex;
}

.timer-btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: 0;
  border-radius: 3px;
  margin-left: 4px;
  margin-right: 4px;
  padding: 10px 20px;
  font-size: 14px;
  display: inline-flex;
}

.timer-btn.is-active {
  background-color: #2196f3;
}

